.backdrop {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
}

.close-location {
  width: 30px;
  height: 30px;
  background: #fff;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-location {
  max-width: 100%;
  background-color: #fff;
  border: 4px solid #d0d021;
  border-radius: 4px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.modal-title-location {
  text-align: center;
  letter-spacing: .03em;
  color: #000;
  margin-bottom: 12px;
  font-size: 34px;
  font-weight: 700;
  line-height: 20px;
}

